<template>
  <div class="page-container">
    <div class="main-content">
      <nav class="navbar user-info-navbar" role="navigation">
        <ul class="user-info-menu left-links list-inline list-unstyled">
          <li class="hidden-sm hidden-xs">
            <router-link to="/" class="btn btn-white btn-sm"></router-link>
          </li>
        </ul>
      </nav>

      <div class="row">
        <div class="col-md-12">
          <div class="panel panel-default">
            <div class="panel-heading">
              <h3 class="panel-title"></h3>
            </div>
            <div class="panel-body">
              <form @submit.prevent="handleSubmit" class="form-horizontal">
                <div class="form-group">
                  <label class="col-sm-2 control-label"></label>
                  <div class="col-sm-10">
                    <select v-model="form.categoryIndex" class="form-control">
                      <option v-for="(category, index) in categories" 
                              :key="index" 
                              :value="index">
                        {{ category.name }}
                      </option>
                    </select>
                  </div>
                </div>

                <div class="form-group">
                  <label class="col-sm-2 control-label"></label>
                  <div class="col-sm-10">
                    <input type="text" v-model="form.title" class="form-control" required>
                  </div>
                </div>

                <div class="form-group">
                  <label class="col-sm-2 control-label"></label>
                  <div class="col-sm-10">
                    <input type="url" v-model="form.url" class="form-control" required>
                  </div>
                </div>

                <div class="form-group">
                  <label class="col-sm-2 control-label">Logo</label>
                  <div class="col-sm-10">
                    <input type="url" v-model="form.logo" class="form-control" required>
                  </div>
                </div>

                <div class="form-group">
                  <label class="col-sm-2 control-label"></label>
                  <div class="col-sm-10">
                    <input type="text" v-model="form.desc" class="form-control">
                  </div>
                </div>

                <div class="form-group">
                  <div class="col-sm-offset-2 col-sm-10">
                    <button type="submit" class="btn btn-info"></button>
                  </div>
                </div>
              </form>
            </div>
          </div>

          <!--  -->
          <div class="panel panel-default">
            <div class="panel-heading">
              <h3 class="panel-title"></h3>
            </div>
            <div class="panel-body">
              <div v-for="(category, categoryIndex) in categories" :key="categoryIndex">
                <h4>{{ category.name }}</h4>
                <table class="table">
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(site, siteIndex) in category.web" :key="siteIndex">
                      <td>{{ site.title }}</td>
                      <td>{{ site.url }}</td>
                      <td>{{ site.desc }}</td>
                      <td>
                        <button class="btn btn-danger btn-sm" 
                                @click="deleteSite(categoryIndex, siteIndex)">
                          
                        </button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import itemsData from '../assets/data.json'

export default {
  name: 'Manage',
  data() {
    return {
      categories: itemsData,
      form: {
        categoryIndex: 0,
        title: '',
        url: '',
        logo: 'https://img1.tucang.cc/api/image/show/e1306a391e2a2a324370bfee481f497b',
        desc: ''
      }
    }
  },
  methods: {
    handleSubmit() {
      // 
      const newSite = {
        title: this.form.title,
        url: this.form.url,
        logo: this.form.logo,
        desc: this.form.desc
      }

      // 
      this.categories[this.form.categoryIndex].web.push(newSite)

      // 
      this.saveToFile()

      // 
      this.form.title = ''
      this.form.url = ''
      this.form.logo = ''
      this.form.desc = ''

      alert('!')
    },

    deleteSite(categoryIndex, siteIndex) {
      if(confirm('?')) {
        this.categories[categoryIndex].web.splice(siteIndex, 1)
        this.saveToFile()
      }
    },

    async saveToFile() {
      try {
        const response = await fetch('http://localhost:3000/api/save', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.categories)
        })
        
        const result = await response.json()
        if(response.ok) {
          alert('!')
        } else {
          throw new Error(result.error)
        }
      } catch(error) {
        alert(': ' + error.message)
      }
    }
  }
}
</script>

<style scoped>
.form-group {
  margin-bottom: 15px;
}
</style> 